<template>
  <div class="warp">
    <HEADER></HEADER>
    <div class="con">
      <ul>
        <li v-for="item in list" :key="item.id">
          <div class="left">
            <div class="title">
              <!-- <p>健康评分：<span>{{item.score}}</span>分</p> -->
              <p>姓名：<span>{{item.testUserName}}</span></p>
            </div>
            <div class="time">
              <img src="../../assets/time.png" alt="">
              <!-- <span>{{item.time}}</span> -->
              <span>{{formatDate(new Date(item.gmtCreate))}}</span>
            </div>
          </div>
          <div class="right">
            <div class="callback" @click="toReport(item.resultId)">查看报告</div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import HEADER from '@/components/header.vue'
import util from '../../common/util'
import {
  getTestResultHistoryUsingGET,
  getHistoryDetialUsingPOST
} from '@/../client/javascript/vue-api-client.js'

  export default {
    name: 'detehistory',
    components: {HEADER},
    data() {
      return {
        list: []
      }
    },
    methods: {
      deteHistory() {
        getTestResultHistoryUsingGET ({}).then(response => {
          if(response.data.code === 0){
            this.list = response.data.list
          }
        })
      },
      formatDate (t) {
        // return util.formatDate.format(new Date(time), 'yyyy-MM-dd hh:mm:ss')
        return t.getFullYear()+"-"+t.getMonth()+"-"+t.getDate()+"  "
        +t.getHours()+":"+t.getMinutes()+":"+t.getSeconds()
      },
      toReport(id) {
        this.$store.commit('setHealthDetail', id)
        getHistoryDetialUsingPOST ({
          param: {
            resultId: this.$store.state.healthDetail.resultId,
            viewType: "urine_details"
          }
        }).then(response => {
          if(response.data.code === 0){
            this.$store.commit('setReportHealth', response.data.body)
            this.$router.push({name: 'repo', params: {goto: 'repo'}});
          }
        })
      }
    },
    mounted() {
      this.deteHistory()
    }
  }
</script>

<style lang="less" scoped>
.warp{
  height: 100%;
  .con {
    height: 100%;
    overflow-y: scroll;
    ul {
      list-style-type: none;
      padding: 0;
      margin: 50px 5%;
      border-top: 1px solid rgb(240, 240, 240);
      li {
        height: 5rem;
        border-bottom: 1px solid rgb(240, 240, 240);
        display: flex;
        .left {
          display: flex;
          flex: 2;
          flex-direction: column;
          justify-content: space-around;
          align-items: flex-start;
          padding: 0.5rem 0;
          .title {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            margin-bottom: 0.7rem;
            span {
              font-size: 1.2rem;
            }
          }
          .time {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            height: 1rem;
            img {
              width: 1rem;
              height: 1rem;
              margin-right: 0.75rem;
            }
            span {
              height: 1rem;
              font-size: 0.85rem;
              color: rgb(184, 184, 184);
            }
          }
        }
        .right {
          display: flex;
          flex: 1;
          justify-content: center;
          align-items: center;
          .callback {
            font-size: 0.75rem;
            color: rgb(80, 126, 165)
          }
        }
      }
    }
  }
}
</style>



